/*Nesting - Anidamiento*/
* {
	margin: 10px;
	padding: 0;
	box-sizing: border-box;
}

.contenedor{
    max-width: 1000px;
	width: 90%;
	margin: 0 auto;
}
.tarjeta{
        background-color: #EDC02C;
        border-radius: 15px;
        padding: 20px;
        display: flex;
        align-items: center;
        position: relative;
        margin-bottom: 50px;

        a {
            text-decoration: none;

            &:hover{
                text-decoration: underline;
            }
        }
}
.tarjeta__img{
    width: 300px;
    height: 180px;
    margin-right: 30px;
}

.tarjeta__titulo {
	font-size:1.5rem;
	margin-bottom: 20px;
}

.tarjeta__parrafo {
	line-height: 28px;
	font-size:1rem;
}

